.card-ppc {
  height: 100%;

  .img-wrapper {
    box-shadow: $box-shadow-value;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    height: calc(100% - 40px);

    img {
      width: 100%;
    }
  }

  .card-desc {
    display: grid;
    grid-template-columns: 1fr 90px;
    // justify-content: space-between;
    font-size: 14px;
    position: relative;
    width: 100%;
    // background: blue;
    max-height: 22px;

    .card-category {
      padding-right: 5px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .see-all-at-category {
      color: $blue-primary;
      font-weight: bold;
      cursor: pointer;
      transition: 0.1s linear;

      &:hover {
        filter: $filter-grey-text;
      }
    }
  }

}