.form-navbar {
  width: 100%;
  max-width: 550px;
  height: 50px;
  display: flex;
  background: $grey-bg;
  padding: 15px 30px;
  padding: clamp(10px, 0.5vw, 15px) clamp(25px, 1vw, 30px);
  border-radius: 12px;

  input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;

    &:focus {
      outline: none;
    }
  }

  span.magnify-icon {
    display: flex;
    align-items: center;
    font-size: 20px;
    color: $grey-text;
    margin-left: 5px;
  }
}