.categories {
  width: 100%;
  height: 100px;
  padding: 21px 50px;
  background: #fff;
  display: flex;
  align-items: center;
  box-shadow: $box-shadow-value;
  border-radius: 12px;
  margin-bottom: 40px;

  &-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    .category {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      height: 58px;
      cursor: pointer;

      span {
        font-size: 14px;
      }
    }
  }

  &-see-all {
    font-size: 12px;
    color: $blue-primary;
    font-weight: bold;
    width: 89px;
    min-width: 89px;
    margin-left: 87px;
    margin-left: clamp(60px, 5vw, 87px);
    cursor: pointer;

    &:hover {
      filter: $filter-grey-text;
    }
  }
}