.todays-promo {
  width: 100%;
  position: relative;

  &-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    h2 {
      font-size: 20px;
      display: flex;
      align-items: center;

      span {
        margin-left: 20px;
        font-size: 14px;
        color: $grey-text;
        font-weight: normal;
        height: 100%;

        img {
          position: relative;
          top: 2px;
        }

        p {
          display: inline-block;
        }
      }
    }

    .see-all-at-promo {
      font-size: 14px;
      color: $blue-primary;
      font-weight: bold;
      cursor: pointer;
      transition: 0.1s linear;

      &:hover {
        filter: $filter-grey-text
      }
    }
  }

  @import './menuSlider';

  &-cards {
    margin-top: 75px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 24px;
  }

  @import './card-todays-promo';
}