.card-todays-promo {
  cursor: pointer;

  .img-wrapper {
    box-shadow: $box-shadow-value;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;

    .discount {
      position: absolute;
      top: 10px;
      right: 10px;
      background: $red-accent;
      padding: 0 10px;
      color: #fff;
      border-radius: 5px;
      font-weight: bold;
      font-size: 12px;
    }

    img {
      width: 100%;
    }
  }

  .card-desc {
    display: grid;
    grid-template-columns: 1fr 100px;
    grid-template-rows: 22px 1fr;
    grid-template-areas:
      'topLeft topRight'
      'bottomLeft bottomRight';
    font-size: 14px;

    // &-left {


    .product-name {
      grid-area: topLeft;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-height: 22px;
    }

    .qty-left {
      grid-area: bottomLeft;
      text-overflow: ellipsis;
      color: $grey-text;

      span {
        font-weight: bold;
      }

      // }
    }

    // &-right {
    // text-align: right;
    // white-space: nowrap;

    .discount-price {
      grid-area: topRight;
      font-size: 12px;
      text-decoration: line-through;
      color: $grey-text;
      justify-self: flex-end;
    }

    .actual-price {
      grid-area: bottomRight;
      font-weight: bold;
      color: red;
      justify-self: flex-end;
    }

    // }
  }
}