.dropdown-category {
  position: absolute;
  transform: translateX(-100%);
  left: -20px;
  height: 100%;
  display: flex;
  align-items: center;
  z-index: 1;

  .btn-dd-title {
    background: transparent;
    font-weight: bold;
    border: none;
    color: $grey-text;
    cursor: pointer;
    transition: 0.1s all linear;

    &:hover {
      filter: $filter-grey-text;
    }

    span {
      margin-left: 10px;
    }
  }

  .dd-menus {
    position: absolute;
    bottom: 0;
    transform: translateY(calc(100% + 15px));
    z-index: $z-index-dd-menus;
    background: #fff;
    width: 250px;
    border-radius: 0 0 12px 12px;
    padding: 22px 30px;
    left: -29px;

    .dd-menu {
      position: relative;
      margin-bottom: 9px;
      font-size: 14px;
      color: $grey-text;
      transition: 0.1s linear;
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      z-index: $z-index-dd-menus;

      &:hover {
        filter: $filter-grey-text
      }

      &:last-child {
        margin-bottom: 0;
      }

      .subdropdown-icon {
        transform: rotate(-90deg);
      }
    }
  }
}