.header-footer{
  
.footer {
  width: 100%;
  padding-inline: 10px;
  box-shadow: 2px 1px 10px #00000029;
  align-items: center;
  .footer-wrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;

    .footer-content {
      display: flex;
      padding-block: 40px;
      justify-content: space-between;
      gap: 14px;
      width: 65%;

      .footer-text {
        text-align: left;
        font: normal normal normal 14px/20px Poppins;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;
      }
    }
    .follow {
      display: flex;
      gap: 30px;
      // background-color: red;
      // padding-right: 20px;
    }
  }
}

.under-footer{
  display: flex;
  padding-block: 20px;
  text-align: left;
font: normal normal normal 12px/16px Poppins;
letter-spacing: 0.6px;
color: #707070;
opacity: 1;
}
}