.about-us {
  margin-bottom: 40px;

  &-title {
    font-size: clamp(10px, 2vw, 14px);
    font-weight: bold;
    color: #000;
    margin-bottom: 10px;
  }

  &-desc {
    font-size: clamp(10px, 2vw, 14px);
    color: $grey-text;
    margin-bottom: 10px;
  }

  &-link {
    font-size: clamp(10px, 2vw, 14px);
    font-weight: bold;
    color: $blue-primary;
    transition: 0.1s all linear;

    &:hover {
      filter: $filter-grey-text;
    }
  }
}