@import './mixins';
@import './variables';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}

ul,
li {
  list-style: none;
}

button:focus {
  outline: none;
}


.container {
  padding: 0 7%;
  margin: auto;
  max-width: $max-width;
  min-width: $min-width;
}

body {
  #root {
    overflow-x: hidden;
    @import './hero/hero';
    @import './hightlight/hightlight';
    @import './big-cart';
    @import './partners';
    @import './about';
    @import './footer';
  }
}