.menu-slider {
  overflow: hidden;
  position: absolute;
  width: 100%;

  a {
    padding: 5px 20px;
    background-color: $grey-bg;
    border-radius: 12px;
    color: $grey-text;
    font-size: clamp(10px, 2vw, 14px);
    font-weight: bold;

    &:focus {
      outline: unset;
    }
  }

  .slick-slider {


    .slick-list {

      .slick-track {
        width: max-content !important;

        .slick-slide {
          margin-right: 10px;

          &:hover {

            a {
              transition: all 0.1s linear;
              filter: $filter-grey-text;
            }

            a.active {
              filter: unset;
            }
          }

          .active {
            background-color: $blue-primary;
            color: #fff;
          }
        }
      }
    }

    .slick-next {
      position: absolute;
      z-index: $z-index-slick-next-prev;
      top: -14%;
      right: 18px;
      transform: scale(0.8);
      height: 42.5px;
      width: 42.5px;
      width: 13px;

      &::before {
        // font-size: 50px;
        opacity: 1;
        content: "";
        display: block;
        height: 35px;
        width: 35px;
        background-size: 35px 35px;
        background-image: url('../../assets/arrow.svg');
        background-repeat: no-repeat;
        transform: scale(1.7) rotate(180deg);
      }
    }

    .slick-prev {
      position: absolute;
      z-index: $z-index-slick-next-prev;
      top: -14%;
      left: 0;
      transform: scale(0.8);
      height: 42.5px;
      width: 13px;

      &::before {
        // font-size: 50px;
        opacity: 1;
        content: "";
        display: block;
        height: 35px;
        width: 35px;
        background-size: 35px 35px;
        background-image: url('../../assets/arrow.svg');
        background-repeat: no-repeat;
        transform: scale(1.7);
      }
    }
  }
}