.product-per-category {
  width: 100%;
  position: relative;

  &-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    h2 {
      font-size: 20px;
    }

    .see-all-at-category {
      font-size: 14px;
      color: $blue-primary;
      font-weight: bold;
      cursor: pointer;
      transition: 0.1s linear;

      &:hover {
        filter: $filter-grey-text
      }
    }
  }

  @import './menuSlider';

  .ppc-cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      'left rightTop'
      'left rightBottom';
    height: 600px;
    gap: 32px 24px;
    margin-top: 75px;

    @import './card-product-per-category';

    .card-ppc:nth-child(1) {
      grid-area: left;
    }

    .card-ppc:nth-child(2) {
      grid-area: rightTop;
    }

    .card-ppc:nth-child(3) {
      grid-area: rightBottom;
    }

  }

}