.header-menu {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 40px;

  &>*+* {
    margin-left: 40px;
  }

  .menu-item {
    color: $grey-text;
    font-size: 14px;
    font-weight: bold;
    position: relative;
    cursor: pointer;
    transition: 0.1s linear;

    &:hover {
      filter: $filter-grey-text;
    }

    &.active {
      color: $blue-primary;

      &::after {
        content: '';
        width: 5px;
        height: 5px;
        background: $blue-primary;
        border-radius: 50%;
        position: absolute;
        bottom: -9px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
}