.navbar-wrapper {
  background: #fff;
  position: relative;
  z-index: $z-index-dd-menus;

  .navbar {
    height: 95px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    font-size: 14px;
    background: #fff;
    padding-top: 15px;

    &-left {
      .logo {
        color: $blue-primary;
        font-size: 18px;
        cursor: pointer;
      }
    }

    &-center {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 45%;
      display: flex;
      justify-content: center;
      align-items: center;

      @import './dropdown-category';
      @import './form-navbar';
    }

    &-right {
      position: absolute;
      right: 0;
      display: flex;
      align-items: center;

      * {
        margin-left: 30px;

        * {
          margin-left: 0;
        }
      }

      @import './cart';
      @import './like';

      .login {
        padding: 7px 20px;
        background: $blue-primary;
        border-radius: 12px;
        color: #fff;
        font-weight: bold;
        transition: 0.1s all linear;

        &:hover {
          filter: brightness(90%);
        }
      }

      .signup {
        font-weight: bold;
        color: $grey-text;
        transition: 0.1s linear;

        &:hover {
          filter: brightness(60%);
        }
      }
    }
  }
}