.banner {
  position: relative;
  height: 300px;
  margin-bottom: 30px;

  .banner-slider {
    height: 300px;
    border-radius: 12px;
    box-shadow: 2px 1px 10px #00000029;
    opacity: 1;
  }

  .img-slider {
    border-radius: 12px;
    max-width: 100%;
    height: 300px;
  }

  .slick-dots {
    transform: translateY(-200%);
  }

  .slick-dots li button:hover:before,
  .slick-dots li button:focus:before {
    opacity: 0.5;
  }

  .slick-dots li button:before {
    font-size: 15px;
    color: #fff;
    opacity: 1;
  }

  .slick-dots li.slick-active button:before {
    color: #026bb9;
    opacity: 1;
  }

  .slick-prev {
    position: absolute;
    left: 40px;
    z-index: $z-index-slick-next-prev;

    &::before {
      font-size: 50px;
      opacity: 1;
    }
  }

  .slick-next {
    position: absolute;
    right: 60px;
    z-index: $z-index-slick-next-prev;

    &::before {
      font-size: 50px;
      opacity: 1;
    }
  }
}