$max-width: 1600px;
$min-width: 996px;

// colors
$blue-primary: #026BB9;
$red-accent: #FF0000;
$grey-bg: #F0F0F0;
$grey-text-placeholder: #A3A3A3;
$grey-text: #707070;
$grey-boxShadow: #00000029;
$overlay: #00000080;

// property value
$box-shadow-value: 2px 1px 10px $grey-boxShadow;
$filter-grey-text: brightness(60%);

// z-indexes value
// .dd-menus @ _dropdown-category.scss
$z-index-dd-menus: 1000;

// .dd-menus::after @ _dropdown-category.scss
$z-index-overlay-dd-menus: 999;

// .slick-prev & .slick-next @ _banner.scss
$z-index-slick-next-prev: 90;