.partners {
  box-shadow: $box-shadow-value;
  padding: 24px 0px;
  margin-bottom: 40px;

  &-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &-left {
    display: flex;
  }

  &-icon {
    margin-right: 20px;
  }

  &-content {
    display: flex;
    flex-direction: column;
    margin-right: 20px;

    &-title {
      color: #000;
      font-size: clamp(12px, 2vw, 16px);
      font-weight: bold;
    }

    &-desc {
      color: $grey-text;
      font-size: clamp(10px, 2vw, 14px);

      span {
        color: $blue-primary;
        font-size: clamp(10px, 2vw, 14px);
        font-weight: bold;
      }
    }
  }

  &-button {
    background-color: $blue-primary;
    border-radius: 5px;
    padding: 8px 20px 7px 20px;
    color: #fff;
    font-weight: bold;
    font-size: clamp(10px, 2vw, 14px);
    transition: 0.1s all linear;
    border: unset;
    cursor: pointer;

    &:hover {
      filter: $filter-grey-text;
    }
  }

  &-link {
    text-decoration: unset;
    color: $blue-primary;
    font-weight: bold;
    font-size: clamp(10px, 2vw, 14px);

    &:hover {
      filter: $filter-grey-text;
    }
  }
}